<template>

  <el-main style="background-color: #000000;color:white;font-family: 'Noto Sans SC';font-weight: 300;">
    <el-row type="flex" justify="center" style="width:100%;">
      <el-col :xs="24" :sm="24" :md="18" :xl="18">
        <el-row>
          <el-col :xs="24" :sm="10">
            <el-row type="flex" justify="flex-start">
              <div :span="16"
                   style="text-align:left;color:#ffffff;font-size:18px;margin-top:20px;margin-left:20px;">

                <div style="display: inline-block;color:#E12968;">
                  {{ $t('player') }} {{ showAccount }}
                </div>
                <div style="font-weight: 300;">
                  {{ $t('accountInfo') }}
                </div>
              </div>
            </el-row>
          </el-col>


          <el-col :xs="24" :sm="{span:14}">

            <el-row style="margin-top:20px;">
              <el-col :xs="{span:11}" :sm="{span:8,offset:7}" v-if="isExistTimelock">
                <vs-button block @click.stop="showTimeoutDepositDialog"
                           style="font-size:20px;line-height:40px;background:linear-gradient(90deg, #E91E63, #FC9E7C);"
                           color="#E91E63">
                  {{ $t('timerDepositButton') }}
                </vs-button>
              </el-col>
              <el-col :xs="{span:11,offset:2}" :sm="{span:8,offset:1}" v-if="isExistTimelock">
                <vs-button block @click.stop="showTimeoutWithdrawDialog"
                           border style="font-size:20px;line-height:40px;"
                           color="#E91E63">
                  {{ $t('timerWithdrawButton') }}
                </vs-button>
              </el-col>
            </el-row>
          </el-col>
        </el-row>
      </el-col>
    </el-row>

    <div
        style="padding-top:-80px;padding-bottom:300px;background:url('home-bg-2.png');background-repeat:no-repeat;background-size:100%;background-position:right;width:100%;">
    </div>

    <el-row type="flex" justify="center" style="margin-top:-270px;">
      <el-col :xs="24" :md="18" :xl="18" style="line-height:40px;">
        <div class="shadow-border">
          <el-row>
            <el-col :xs="24" :sm="24" :lg="24">
              <div style="color:#ffffff;">

                <el-row type="flex" justify="center">
                  <el-col :xs="24" :md="22" style="line-height:60px;">
                    <div class="me-shadow-border" style="margin-top:30px;margin-bottom: 30px;">

                      <el-row type="flex"
                              style="flex-wrap:wrap;vertical-align:center;justify-content:center;align-items:center;">
                        <el-col :xs="24" :sm="{span:14,offset:1}"
                                style="text-align: center;font-size:20px;margin-top:-20px;">
                          {{ $t('depositMoney') }}
                          <div
                              style="text-align:center;font-size:50px;font-weight: 600;color: #FFFFFF;background: linear-gradient(77.7398deg, #C9225B 0.9521484375%, #FF3278 60.6689453125%, #FC9E7C 100%);-webkit-background-clip: text;-webkit-text-fill-color: transparent; letter-spacing:8px">
                            ${{ totalUserMoney }}
                          </div>

                        </el-col>
                        <el-col :xs="24" :sm="{span:9}" style="border-left:1px dashed #708090;">
                          <img src="money-me.png" style="width: 100%;">
                        </el-col>

                      </el-row>

                    </div>
                  </el-col>
                </el-row>
                <el-row>
                  <div style="">
                  </div>
                </el-row>

              </div>
            </el-col>


          </el-row>
        </div>
      </el-col>
    </el-row>

    <el-row type="flex" justify="center"
            style="flex-wrap:wrap;margin-top:40px;background:url('home-bg-3.png');background-repeat:no-repeat;background-size:100% ;background-position:left;">
      <el-col :xs="24" :sm="24" :md="18" :lg="9" style="line-height:40px;margin-top:20px;"
              v-for="(poolTicket,i) in poolTicketArray" :key="i">
        <div
            style="background-image:url('ticket.png');background-size:100% 100%;background-repeat:no-repeat;height:250px;">
          <el-row type="flex" justify="flex-start"
                  style="color:#DD2866;font-size:24px;padding-top:40px;text-align: left;padding-bottom:30px;">
            <el-col type="flex" :span="16" :offset="2">
              <el-row style="font-size:30px;font-weight: 400;color:#ffffff;">
                {{ poolTicket.ticketNumber }}
              </el-row>
              <el-row style="font-size:13px;color:#CECECE">
                {{ $t('meSentence1') }}
              </el-row>
              <el-row style="font-size:13px;margin-top:-10px;">
                {{ poolTicket.chance }}%
              </el-row>
              <el-row type="flex" style="margin-top:30px;align-content:flex-end;align-self:flex-end;color:#ffffff;">
                <el-col :span="9" style="font-weight: 400;">
                  ${{ poolTicket.poolAwardMoney }}
                </el-col>
                <el-col :span="3">
                  <img src="logo-white.png" style="width:25px;">
                </el-col>
                <el-col :span="12" style="color:#CECECE;font-size:12px;line-height:20px;">
                  <el-row>
                    {{ $t('meSentence2') }}
                  </el-row>
                  <el-row>
                    {{ poolTicket.formatTime }}

                  </el-row>
                </el-col>
              </el-row>
            </el-col>
            <el-col :span="4" :offset="2" style="color:#FFFFFF;margin-top:20px;font-size:20px;">
              <img style="text-align:left;width:40px;"
                   src="cfx-logo.svg"/>
              <el-row>
                PCFX
              </el-row>
            </el-col>
          </el-row>
        </div>
      </el-col>
    </el-row>


    <el-row type="flex" justify="center" style="margin-top:50px">
      <el-col :xs="24" :md="18" :xl="18" style="line-height:40px;">
        <div class="shadow-border">
          <el-row type="flex" justify="center" style="margin-top:40px">
            <el-col :xs="24" :md="22" :xl="22" style="line-height:40px;">
              <el-row style="">
                <el-col :span="12" style="font-weight: 500;font-size:30px;text-align: left;padding-left:10px;">
                  <el-row style="color:#DD2866">
                    {{ totalUnclaimedPoolGoNum }}
                  </el-row>
                  <el-row style="font-size:15px;font-weight: 200;">
                    {{ $t('meSentence3') }}
                  </el-row>
                </el-col>
                <el-col :span="12" style="font-size:14px;text-align: right;margin-top:10px;padding-right:10px;">
                  <el-row type="flex" justify="end">
                    <el-col :xs="24" :sm="9">
                      <vs-button block border style="font-size:16px;line-height:20px;" @click="claimAll"
                                 color="#E91E63">
                        {{ $t('claimAll') }}
                      </vs-button>
                    </el-col>
                  </el-row>
                  <el-row >
                    <div @click="showGovernmentMorePage" style="cursor:pointer;">
                    {{ $t('meSentence4') }}
                    </div>
                  </el-row>
                </el-col>
              </el-row>
              <el-row type="flex" justify="start"
                      style="border:2px dashed #E91E63;border-radius:10px;padding-bottom:30px;margin-top:30px;;margin-bottom:30px;">
                <el-col :xs="{span:4}" :sm="{span:3,offset:1}" style="margin-top:30px;">
                  <img src="cfx-logo.svg" style="width:60%">
                </el-col>
                <el-col :xs="13" :sm="8" style="font-size:14px;line-height:30px;text-align:left;padding-left:10px;">
                  <el-row style="font-size:24px;margin-top:30px;font-weight: 400;">
                    {{ $t('cfxPrize') }}
                  </el-row>
                  <el-row style="color:#B7B7B7">
                    PCFX
                  </el-row>
                  <el-row style="color:#B7B7B7">
                    {{ $t('meSentence5') }}{{ daliyPoolGo }} POOLGO /{{ $t('days') }}
                  </el-row>
                  <el-row style="color:#B7B7B7">
                    *％ {{ $t('apy') }}
                  </el-row>
                </el-col>
                <el-col :xs="6" :sm="{span:4,offset:7}" style="text-align: right;margin-top:26px;line-height:30px;">
                  <el-row class="change-font-size">
                    {{ $t('meSentence6') }}
                  </el-row>
                  <el-row type="flex" justify="end" style="font-size: 24px;color:#DA1D5E">
                    <div style="text-align:right;">
                      <img style="text-align:right;width:22px;"
                           src="poolgo.png"/>
                    </div>
                    <div style="text-align: right;margin-top:-2px;padding-left:10px;"  class="change-poolgo-font-size">
                      {{ unclaimedCfxPoolGoNum }}
                    </div>
                  </el-row>
                  <el-row style="color:#B7B7B7;font-size:14px;">
                    {{ daliyUserPoolGo }} POOLGO /{{ $t('days') }}
                  </el-row>
                  <el-row>
                    <vs-button block border style="font-size:16px;line-height:20px;" @click="claim"
                               color="#E91E63">
                      {{ $t('claim') }}
                    </vs-button>
                  </el-row>
                </el-col>
              </el-row>


            </el-col>
          </el-row>
        </div>
      </el-col>
    </el-row>
    <el-row type="flex" justify="center"
            style="padding-top:50px;padding-bottom:200px;background-image:url('home-bg-4.png');background-repeat:no-repeat;background-position:right bottom;width:100%;"
            class="partner-background">
      <el-col :xs="24" :md="18" :xl="18" style="line-height:40px;">
        <div class="shadow-border">
          <el-row type="flex" justify="center" style="margin-top:40px">
            <el-col :xs="24" :md="22" :xl="22" style="line-height:40px;">
              <el-row style="">
                <el-col :span="12" style="font-weight: 500;font-size:30px;text-align: left;padding-left:10px;">
                  {{ $t('prize') }}
                </el-col>
              </el-row>
              <div style="width:100%;height: 1px;background-color:  #E91E63;margin-top:20px;"></div>
              <el-row type="flex" align="middle" style="margin-top:10px;">
                <el-col :xs="12" :sm="12">
                  <el-row type="flex" justify="start" style="font-size: 26px;color:#FFFFFF"
                          v-if="userAwardInfoArray.length==0">
                    {{ $t('meSentence7') }}
                  </el-row>
                  <el-row v-else>
                    <el-row type="flex" justify="start" style="font-size: 26px;color:#FFFFFF"
                            v-for="(userAwardInfo,i) in userAwardInfoArray" :key="i">
                      <div style="text-align:right;">
                        <img style="text-align:right;width:30px;"
                             src="cfx-logo.svg"/>
                      </div>
                      <div style="text-align: right;margin-top:-4px;padding-left:20px;">
                        {{ userAwardInfo.formatAmount }} &nbsp;&nbsp;PCFX
                      </div>
                    </el-row>
                  </el-row>
                </el-col>
                <el-col :xs="12" :sm="12" style="text-align: right;">
                  <img src="prize.png" style="width:150px;">
                </el-col>
              </el-row>

            </el-col>
          </el-row>
        </div>
      </el-col>
    </el-row>
    <el-row type="flex" justify="center" style="margin:0 auto;color:#999999">

      <el-col :xs="24" :sm="8">
        <vs-button block border style="font-size:16px;line-height:20px;margin-top:-140px;"
                   color="#E91E63" @click="showConfluxScanPage">
          {{ $t('meSentence8') }}
        </vs-button>
      </el-col>
    </el-row>
    <vs-dialog :loading="timelockDepositDialogLoading" width="550px" not-center prevent-close
               v-model="timelockDepositDialogShow">
      <template #header>
        <h4 class="not-margin">
          {{ $t('meSentence9Part1') }}<b>{{ $t('meSentence9Part2') }}</b>
        </h4>
      </template>


      <div class="con-content">
        <p style="text-align:left">
          {{ $t('meSentence10') }}
        </p>
        <vs-input icon-after v-model="timelockBalance" :placeholder="'当前可重新存入奖池的PCFX数量为'+timelockUserBalance.toFormat(2)"
                  type="number">
          <template #icon>
            <vs-button @click="maxTimelockBalance" circle flat
                       warn
            >
              {{$t('max')}}
            </vs-button>
          </template>
        </vs-input>

      </div>

      <template #footer>
        <div class="con-footer">
          <vs-button @click="timeoutDeposit" transparent>
            {{ $t('noticeConfirm') }}
          </vs-button>
          <vs-button @click="timelockDepositDialogShow=false" dark transparent>
            {{ $t('noticeCancel') }}
          </vs-button>
        </div>
      </template>
    </vs-dialog>
    <vs-dialog :loading="timelockWithdrawDialogLoading" width="550px" not-center prevent-close
               v-model="timelockWithdrawDialogShow">
      <template #header>
        <h4 class="not-margin">
          {{ $t('meSentence11Part1') }}<b>{{ $t('meSentence11Part2') }}</b>
        </h4>
      </template>


      <div class="con-content">
        <p style="text-align:left">
          {{ $t('meSentence12Part1') }}{{ timelockUserBalance.toFormat(2) }}{{ $t('meSentence12Part2') }}
        </p>
      </div>

      <template #footer>
        <div class="con-footer">
          <vs-button @click="withdrawDeposit" transparent>
            {{ $t('noticeConfirm') }}
          </vs-button>
          <vs-button @click="timelockWithdrawDialogShow=false" dark transparent>
            {{ $t('noticeCancel') }}
          </vs-button>
        </div>
      </template>
    </vs-dialog>
  </el-main>

</template>


<script>
import config from "@/config";
import utils from '@/utils/utils.js'
import {mapActions} from "vuex";
import {BigNumber} from "bignumber.js";


export default {
  name: "me",
  components: {},
  data() {
    return {
      poolAwardMoney: 0,
      ticketNumber: 0,
      poolTicketArray: [],
      unclaimedCfxPoolGoNum: 0,
      daliyPoolGo: 0,
      daliyUserPoolGo: 0,
      userAwardInfoArray: [],
      totalUserMoney: 0,
      pcfxApy: 0,
      timelockUserBalance: new BigNumber(0),
      timelockUserBalanceFormat: 0,
      isExistTimelock: false,
      isExistWithdrawTimelock: false,
      timelockUserAvaliableAt: null,
      timelockDepositDialogShow: false,
      timelockBalance: '',
      timelockDepositDialogLoading: false,
      timelockWithdrawDialogShow: false,
      timelockWithdrawDialogLoading: false,
      intervalTimer: null,
      secondTimer:null,
      isFirstRun:true,
      dripRatePerSecond:0,
      formatTime:0,
      prizePeriodRemainingSeconds:0,

    };
  },
  computed: {

    totalUnclaimedPoolGoNum: function () {
      return this.unclaimedCfxPoolGoNum;
    },
    pcfxContract: function () {
      return this.$store.state.web3.pcfxContract;
    },
    ticketContract: function () {
      return this.$store.state.web3.ticketContract;
    },
    poolGoContract: function () {
      return this.$store.state.web3.poolGoContract;
    },
    prizePoolContract: function () {
      return this.$store.state.web3.prizePoolContract;
    },
    periodicPrizeStrategyContract: function () {
      return this.$store.state.web3.periodicPrizeStrategyContract;
    },
    tokenFaucetContract: function () {
      return this.$store.state.web3.tokenFaucetContract;
    },
    cfxStakeContract: function () {
      return this.$store.state.web3.cfxStakeContract;
    },
    tokenFaucetProxyFactoryContract: function () {
      return this.$store.state.web3.tokenFaucetProxyFactoryContract;
    },
    showAccount: function () {
      if (this.$store.state.web3.account != null) {
        return utils.shortAddr(this.$store.state.web3.account)
      } else
        return ''
    },
    account: function () {
      return this.$store.state.web3.account;
    }
  },
  watch: {
    account: async function (account) {
    }
  },
  async created() {
    const that = this;
    if (that.$store.state.web3.account == null) {
      await that.cfxLogin();
    }
    if (that.$store.state.web3.isPortalInstalled == false) {
      that.$router.push({
        path: "/pools"
      });
      that.$vs.notification({
        // width:'100%',
        duration: 10000,
        icon: "<i class='bx bx-error' ></i>",
        progress: 'auto',
        color: 'danger',
        position: 'top-center',
        title: that.$t('error'),
        text: that.$t('noticeWallet'),
      })
      return;
    }
    if (that.$store.state.web3.account == null) {

      that.$router.push({
        path: "/pools"
      });
      that.$vs.notification({
        // width:'100%',
        duration: 10000,
        icon: "<i class='bx bx-error' ></i>",
        progress: 'auto',
        color: 'danger',
        position: 'top-center',
        title: that.$t('error'),
        text: that.$t('noticeAddr'),
      })
      return;
    }
    let userAwardInfo = await that.prizePoolContract.getUserAwardInfo(that.$store.state.web3.account);
    let userAwardInfoArray = [];
    let j =0;
    for (let i = 0; i < userAwardInfo.length; i++) {
      if (userAwardInfo[i].amount.gt(0)) {
        userAwardInfoArray[j] = {};
        userAwardInfoArray[j].formatAmount = userAwardInfo[i].amount.toFormat(0);
        j++;
      }
    }
    that.userAwardInfoArray = userAwardInfoArray;

    let dripRatePerSecond = await that.tokenFaucetContract.dripRatePerSecond();
    that.dripRatePerSecond = dripRatePerSecond;
    that.daliyPoolGo = that.dripRatePerSecond.multipliedBy(60 * 60 * 24).toFormat(0);


    let prizePeriodRemainingSeconds = (await that.periodicPrizeStrategyContract.prizePeriodRemainingSeconds()).toNumber()

    that.prizePeriodRemainingSeconds = prizePeriodRemainingSeconds;
    that.formatTime = utils.formatSeconds(that.prizePeriodRemainingSeconds);

    that.updateTimelockInfo();

    that.update();
    that.secondTimer = setInterval(that.countSecond, 1000)
    that.intervalTimer = setInterval(that.update, 6000)
  },

  async mounted() {
  },
  beforeDestroy() {
    var that = this;
    clearTimeout(that.intervalTimer);
    clearTimeout(that.secondTimer);
  },
  methods: {
    ...mapActions(['cfxLogin']),
    showGovernmentMorePage(){
      window.open("https://docs.poolgo.finance/poolgo-finance/governance");
    },
    countSecond(){
      var that = this;
      if (that.prizePeriodRemainingSeconds>0) {
        that.prizePeriodRemainingSeconds--;
        that.formatTime = utils.formatSeconds(that.prizePeriodRemainingSeconds);
        if (that.poolTicketArray.length!=0) {
          that.poolTicketArray[0].formatTime = that.formatTime;
        }
      }else{
        clearTimeout(that.secondTimer);
      }
    },
    showConfluxScanPage() {
      window.open(config.viewOnConfluxScan);
    },
    async showTimeoutDepositDialog() {
      var that = this;
      that.timelockDepositDialogShow = true;
      that.timelockDepositDialogLoading = true;
      let timelockUserBalance = await that.prizePoolContract.timelockBalanceOf(that.$store.state.web3.account);
      that.timelockUserBalance = timelockUserBalance;
      // that.timelockUserBalanceFormat = timelockUserBalance.toFormat(2);
      that.timelockDepositDialogLoading = false;
      that.timelockBalance = '';
    },
    async showTimeoutWithdrawDialog() {
      var that = this;
      let blockStatus = await this.$store.state.web3.confluxReader.getStatus();
      let bestHashBlockStatus = await this.$store.state.web3.confluxReader.getBlockByHash(blockStatus.bestHash);
      if (that.timelockBalanceAvailableAt.gt(bestHashBlockStatus.timestamp)) {
        let waitTime = that.timelockBalanceAvailableAt.minus(bestHashBlockStatus.timestamp);
        let waitTimeFormat = utils.formatSeconds(waitTime.toNumber());
        this.$vs.notification({
          // width:'100%',
          duration: 10000,
          icon: "<i class='bx bx-error' ></i>",
          progress: 'auto',
          color: 'danger',
          position: 'top-center',
          title: that.$t('error'),
          text: that.$t('noticeSentece7Part1')+waitTimeFormat+that.$t('noticeSentece7Part2'),
        })
        return ;
      }
      that.timelockWithdrawDialogShow = true;
      that.timelockWithdrawDialogLoading = true;


      let timelockUserBalance = await that.prizePoolContract.timelockBalanceOf(that.$store.state.web3.account);
      that.timelockUserBalance = timelockUserBalance;
      // that.timelockUserBalanceFormat = timelockUserBalance.toFormat(2);
      that.timelockWithdrawDialogLoading = false;
    },
    async withdrawDeposit() {
      let that = this;

      that.timelockWithdrawDialogLoading = true;
      let status = await that.prizePoolContract.sweepTimelockBalances(that.$store.state.web3.account, [that.$store.state.web3.account]);
      that.timelockWithdrawDialogLoading = false;
      if (status.succ == true) {

        this.$vs.notification({
          // width:'100%',
          duration: 6000,
          icon: "<i class='bx bx-select-multiple' ></i>",
          progress: 'auto',
          color: 'success',
          position: 'top-center',
          title: that.$t('info'),
          text: that.$t('noticeSentece3'),
        })
        // this.showStakeDialog();
      } else {
        this.$vs.notification({
          // width:'100%',
          duration: 10000,
          icon: "<i class='bx bx-error' ></i>",
          progress: 'auto',
          color: 'danger',
          position: 'top-center',
          title: that.$t('error'),
          text: status.message,
        })
        return;

      }

      that.timelockWithdrawDialogShow = false;

      that.updateCfxAndPcfxBalance();
      that.updateTimelockInfo();
    },
    async timeoutDeposit() {
      let that = this;

      if (that.timelockBalance == '' || that.timelockBalance <=0){
        that.$vs.notification({
          // width:'100%',
          duration: 10000,
          icon: "<i class='bx bx-error' ></i>",
          progress: 'auto',
          color: 'danger',
          position: 'top-center',
          title: that.$t('error'),
          text: that.$t('correctNumPlaceholder'),
        })
        return;
      }

      that.timelockDepositDialogLoading = true;
      let status = await that.prizePoolContract.timelockDepositTo(that.$store.state.web3.account, that.$store.state.web3.account, that.timelockBalance, config.ticketAddress);
      that.timelockDepositDialogLoading = false;
      if (status.succ == true) {

        this.$vs.notification({
          // width:'100%',
          duration: 6000,
          icon: "<i class='bx bx-select-multiple' ></i>",
          progress: 'auto',
          color: 'success',
          position: 'top-center',
          title: that.$t('info'),
          text: that.$t('noticeSentece4'),
        })
        // this.showStakeDialog();
      } else {
        this.$vs.notification({
          // width:'100%',
          duration: 10000,
          icon: "<i class='bx bx-error' ></i>",
          progress: 'auto',
          color: 'danger',
          position: 'top-center',
          title: that.$t('error'),
          text: status.message,
        })
        return;

      }

      that.timelockDepositDialogShow = false;

      that.updateCfxAndPcfxBalance();
      that.updateTimelockInfo();
    },
    async claimAll() {
      var that = this;
      let status = await that.tokenFaucetProxyFactoryContract.claimAll(that.$store.state.web3.account, [config.tokenFaucetAddress]);
      if (status.succ == true) {

        that.$vs.notification({
          // width:'100%',
          duration: 6000,
          icon: "<i class='bx bx-select-multiple' ></i>",
          progress: 'auto',
          color: 'success',
          position: 'top-center',
          title: that.$t('info'),
          text: that.$t('meSentence13'),
        })
      } else {
        that.$vs.notification({
          // width:'100%',
          duration: 10000,
          icon: "<i class='bx bx-error' ></i>",
          progress: 'auto',
          color: 'danger',
          position: 'top-center',
          title: that.$t('error'),
          text: status.message,
        })
      }
    },
    async update() {
      var that = this;

      if (that.$store.state.web3.account != null) {
        let ticketNumber = await that.ticketContract.balanceOf(that.$store.state.web3.account);
        let totalSupply = await that.ticketContract.totalSupply();
        that.ticketNumber = ticketNumber.toFormat(1);
        let totalUserMoney = ticketNumber.multipliedBy(that.$store.state.web3.pcfxPrice);
        that.totalUserMoney = totalUserMoney.toFormat(0);

        let totalFunds = await that.cfxStakeContract.balanceOfUnderlyingView();
        let tickets = await that.prizePoolContract.accountedBalance();
        let prizePeriodRemainingSecondsBigNumber = (await that.periodicPrizeStrategyContract.prizePeriodRemainingSeconds())

        let interest = totalFunds.multipliedBy(prizePeriodRemainingSecondsBigNumber.multipliedBy(0.0408).div(31536000));
        // console.log("tickets" + tickets)
        var award = totalFunds.plus(interest).minus(tickets);

        let poolAwardMoney = award.multipliedBy(that.$store.state.web3.pcfxPrice);

        var poolTicket = {};
        poolTicket.ticketNumber = that.ticketNumber;
        poolTicket.chance = (ticketNumber.div(totalSupply).multipliedBy(100)).toFormat(4);
        poolTicket.poolAwardMoney = poolAwardMoney.toFormat(0);
        poolTicket.formatTime = that.formatTime;
        that.poolTicketArray = [];
        that.poolTicketArray.push(poolTicket);

        that.daliyUserPoolGo = (ticketNumber.div(totalSupply).multipliedBy(that.dripRatePerSecond.multipliedBy(60 * 60 * 24))).toFormat(0);
        that.pcfxApy = (new BigNumber(that.dripRatePerSecond * 60 * 60 * 24 * 365))
            .multipliedBy(that.$store.state.web3.poolgoPrice).div(totalSupply / that.$store.state.web3.pcfxPrice).multipliedBy(100).toFormat(1);


        if (that.isFirstRun) {

          that.isFirstRun = false;

          let unclaimedBalance = await that.tokenFaucetContract.unclaimedBalanceOf(that.$store.state.web3.account);
          that.unclaimedCfxPoolGoNum = unclaimedBalance.toFormat(3);

          if (that.timelockUserBalance.gt(0)) {
            that.isExistTimelock = true;
          }else{
            that.isExistTimelock = false;
          }

        } else {
          setTimeout(async function () {

            let unclaimedBalance = await that.tokenFaucetContract.unclaimedBalanceOf(that.$store.state.web3.account);
            that.unclaimedCfxPoolGoNum = unclaimedBalance.toFormat(3);

            // that.timelockUserBalanceFormat = that.timelockUserBalance.toFormat(2);
            if (that.timelockUserBalance.gt(0)) {
              that.isExistTimelock = true;
            }else{
              that.isExistTimelock = false;
            }

          }, 3000);
        }
      }
    },
    async claim() {
      let that = this;
      let status = await that.tokenFaucetContract.claim(this.$store.state.web3.account);
      if (status.succ == true) {
        that.$vs.notification({
          // width:'100%',
          duration: 6000,
          icon: "<i class='bx bx-select-multiple' ></i>",
          progress: 'auto',
          color: 'success',
          position: 'top-center',
          title: that.$t('info'),
          text: that.$t('meSentence13'),
        })
      } else {

        that.$vs.notification({
          // width:'100%',
          duration: 10000,
          icon: "<i class='bx bx-error' ></i>",
          progress: 'auto',
          color: 'danger',
          position: 'top-center',
          title: that.$t('error'),
          text: status.message,
        })
      }
    },

    async updateCfxAndPcfxBalance(){
      let that = this;
      let pcfxBalance = await that.pcfxContract.balanceOf(that.$store.state.web3.account);
      that.$store.commit('UPDATE_PCFX_BALANCE', pcfxBalance);
      let balance = await this.$store.state.web3.confluxReader.getBalance(that.$store.state.web3.account)
      that.$store.commit('UPDATE_BALANCE', BigNumber(balance).div(BigNumber(10 ** 18)))

    },
    async updateTimelockInfo(){
      let that = this;
      let timelockUserBalance = await that.prizePoolContract.timelockBalanceOf(that.$store.state.web3.account);
      let timelockBalanceAvailableAt = await that.prizePoolContract.timelockBalanceAvailableAt(that.$store.state.web3.account);

      that.timelockUserBalance = timelockUserBalance;
      that.timelockBalanceAvailableAt = timelockBalanceAvailableAt;
      if (that.timelockUserBalance.gt(0)) {
        that.isExistTimelock = true;
      }else{
        that.isExistTimelock = false;
      }
      // that.timelockUserBalanceFormat = that.timelockUserBalance.toFormat(2);

    },
    maxTimelockBalance(){
      let that = this;
      that.timelockBalance = that.timelockUserBalance.toFixed(2);
    },
  },
};
</script>

